.result {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
}

.resultHeading {
  font-weight: 700;
  font-size: 22px;
  line-height: 1.36;
  margin: 0;
  margin-top: 40px;
  padding: 0;
}

.testName {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;
  margin: 0;
  padding: 0;
  margin-top: 10px;
}

.horizontalLine {
  margin-top: 30px;
  width: 321.5px;
  border-bottom: 1px solid #d0d4dd;
}

.diagram {
  margin-top: 40px;
}

.textResult {
  display: flex;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  margin: 0;
  padding: 0;
  margin-top: 30px;
}

.textResult p:first-child {
  margin-right: 20px;
  padding-right: 20px;
  border-right: 1px solid #d0d4dd;
}

.textResult p {
  margin: 0;
  padding: 0;
}

.other {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cat {
  width: 176px;
  height: 176px;
}

.shortResult {
  margin: 0;
  padding: 0;
  margin-top: 10px;
  font-weight: 700;
  font-size: 22px;
  line-height: 1.36;
}

.fullResult {
  margin: 0;
  padding: 0;
  margin-top: 10px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
}

.tryAgain {
  min-width: 190px;
  padding: 17px 28px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  border: none;
  margin-top: 47px;
  margin-bottom: 60px;
  background-color: #ff6b09;
  color: #ffffff;
}
